.case {
  display: flex;
  margin-left: 20px;
  margin-bottom: 20px;
  &+.case {
    margin-top: 30px; }
  &__one {
    padding-left: 0;
    padding-right: 10px;
    width: 170px; }
  &__head {
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 33px; }
  &__two {
    flex-direction: column;
    width: 100%;
    a {
      display: block; }
    input {
      width: 100%;
      height: 40px;
      border: 1px solid #afafaf;
      border-radius: 50px;
      padding-left: 30px; } }
  &__input {
    input {
      width: 100%;
      height: 40px;
      border: 1px solid #afafaf;
      border-radius: 50px;
      padding-left: 30px; } }
  &__txt {
    padding: 10px;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px; } }

@media screen and (min-width: 560px) {
  .case {
    &__one {
      padding-left: 10px;
      padding-right: 10px; } } }

@media screen and (min-width: 768px) {
  .case {
    display: flex;
    margin-left: 40px;
    margin-bottom: 40px;
    &+.case {
      margin-top: 60px; }
    &__one {
      padding-left: 40px;
      padding-right: 20px;
      min-width: 170px;
      width: unset; } } }
