  #loader .progress {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 50px;
    color: #FEFEFE; }
  #loader.loaded_hiding {
    transition: 0.3s opacity;
    opacity: 0; }
  #loader.loaded {
    display: none; }

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg); } }
